import React, {Component} from 'react';
import ReactPlayer from 'react-player'
import './galleryPage.css';
import RedirectButton from '../../components/redirectButton/redirectButton';

import {humanlapseGallery} from './../../content/content'


export default class GalleryPage extends Component {

    constructor(props) {
        super(props);

        const key = this.props.match.params.key;
        if (humanlapseGallery[key] === undefined) {
            // TODO: redirect to 404
        }
        this.state = {
            gallery: humanlapseGallery[key],
            galleryIndex: 0,
            loading: 0
        }
    }

    previousContent() {
        this.setState((previousState) => ({galleryIndex: Math.max(previousState.galleryIndex - 1, 0)}))
    }

    nextContent() {
        this.setState((previousState) => ({galleryIndex: Math.min(previousState.galleryIndex + 1, previousState.gallery.content.length - 1)}))
    }

    navigationButtons() {

        const beginningOfGallery = this.state.galleryIndex === 0;
        const endOfGallery = this.state.galleryIndex === this.state.gallery.content.length-1;

        const invisibleButtonStyle = {
            opacity: 0,
            userSelect: 'none'
        };

        if (1 < this.state.gallery.content.length) {
            return (
                <div className='navigationButtons'>
                    <div className="svgButton previousButton"
                         style={beginningOfGallery ? invisibleButtonStyle : {} }
                         onClick={() => {this.previousContent()}}>
                        <div className="previous"/>
                    </div>

                    <div
                        className={'middleText'}> {this.state.galleryIndex + 1} / {this.state.gallery.content.length}</div>
                    <div className="svgButton nextButton"
                         style={endOfGallery ? invisibleButtonStyle : {} }
                         onClick={() => {this.nextContent()}}>
                        <div className="next"/>
                    </div>
                </div>
            )
        } else {
            return (<></>)
        }
    }

    reactPlayerLayoutClass() {
        const contentData = this.state.gallery.content[this.state.galleryIndex].data;

        if( contentData.indexOf('vimeo') !== -1 ) {
            return 'pageLayoutManualLayoutVideoPlayer manualSixteenByNineHeight'
        } else {
            return 'pageLayoutManualLayoutVideoPlayer'
        }
    }

    playerProgress(data) {
        console.log(data.loaded);
    }

    goToMenu() {
        this.props.history.push(process.env.PUBLIC_URL + '/index')
    }

    render() {
        const contentObject = this.state.gallery.content[this.state.galleryIndex];
        const galleryTitle = this.state.gallery.name;
        const galleryDescription = this.state.gallery.description;
        const contentData = contentObject.data;
        const contentCredits = contentObject.credits;

        return (
            <div className="galleryPage">
                <div className="pageLayout">
                    <div className="pageLayoutHeader header">

                        <div className="svgButton exitButton" onClick={() => { this.goToMenu() }}>
                            <div className="exit"/>
                        </div>

                        <h1>{galleryTitle}</h1>
                    </div>


                            <div className="contentText">{galleryDescription}</div>



                    <div className="pageLayoutVisuals" id='pageLayoutVisuals'>
                        <div className={this.reactPlayerLayoutClass()}>
                            <ReactPlayer url={contentData} loop={true} width={'100%'} height={'100%'} controls={true} playing onProgress={this.playerProgress}/>
                        </div>

                    </div>
                    <div className="contentCredits">{contentCredits}</div>
                    <div>{this.navigationButtons()}</div>
                </div>
            </div>
        );
    }
}
